<template>
  <div class="component py-4">
    <h5 class="component__heading pt-0 pt-lg-2 px-3 px-lg-5">Edit your profile Information.</h5>
    <div class="row">
      <div class="col-lg-9 col-xl-7">
        <BetForm class="px-3 px-lg-5">
          <form @submit.prevent="updateProfile()">
            <div class="form-group required">
              <input type="text" class="form-control"
                    v-model="profile.name"
                    required
                    id="name" aria-describedby="phoneHelp" placeholder="Enter Full Names">
            </div>
            <div class="form-group required">
              <input type="text" class="form-control"
                    v-model="profile.country"
                    required
                    id="country" aria-describedby="phoneHelp" placeholder="Enter your Country">
            </div>
            <div class="form-group">
              <div class="form-check form-check-inline">
                <input class="form-check-input" type="radio" name="gender" id="male" value="male" v-model="profile.gender">
                <label class="form-check-label" for="male"> Male </label>
              </div>
              <div class="form-check form-check-inline">
                <input class="form-check-input" type="radio" name="gender" id="female" value="female" v-model="profile.gender">
                <label class="form-check-label" for="female"> Female </label>
              </div>
            </div>
            <div class="form-group required">
              <input type="email" class="form-control"
                    v-model="profile.email"
                    required
                    id="email" aria-describedby="phoneHelp" placeholder="Enter your email">
            </div>

            <div class="form-group d-flex justify-content-between">
              <BetButton type="submit">Update Profile</BetButton>
              <BetButton @click.prevent="$router.push({name: 'profile'})">Skip</BetButton>
            </div>
          </form>
        </BetForm>
      </div>
    </div>
  </div>
</template>

<script>
  import {UPDATE_PROFILE_URL} from '../../api_routes'
  import axios from 'axios'
  import {AUTH_CONFIG} from '../../auth_config'
  import BetForm from '../Utilities/BetForm.vue'
  import BetButton from '../Utilities/BetButton.vue'

export default {
    name: 'EditProfile',
    components: {BetForm, BetButton},
    data () {
      return {
        profile: {
          name: '',
          email: '',
          gender: '',
          country: ''
        },
        responseErrors: null
      }
    },
    computed: {
    },
    mounted () {
      console.log('Edit user profile component mounted')
    },
    methods: {
      updateProfile () {
        axios.post(UPDATE_PROFILE_URL, this.profile, {headers: AUTH_CONFIG.getHeaders()}).then(resp => {
          if (resp.status === 200) {
            let profileResponseData = resp.data
            if (profileResponseData.code === 200) {
              this.$noty.success('Profile updated successfully')
              this.$store.dispatch('init_auth_user', {request: 'refresh'})
            }
            this.$router.push({name: 'profile'})
          }
        }, (error) => {
          this.responseErrors = error.response.errors
          if (!this.responseErrors) {
            this.$noty.error('Unable to Update profile please try again later')
          }
          console.log('Unable to Update profile', error.response.data)
        })
      }
    }
  }
</script>

